export default class BbwzBetGameTool {
    //数组乱序
    public static shuffle(array) {
        return Global.Toolkit.getOutOrderArray(array);
    }

    // 获取(0, 1)之间随机数
    public static random() {
        return Math.random();
    }

    // 获取(-1, 1)之间随机数
    public static signedRandom(){
        return Math.random() * -2 + 1;
    }
}